import {
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"


class CardSearchComponent extends StreamlitComponentBase<any> {

  public render = (): ReactNode => {
    // Arguments that are passed to the plugin in Python are accessible
    // via `this.props.args`
    const score = this.props.args["score"]
    const context = this.props.args["context"]
    const link = this.props.args["link"]
    
    return (
      <div>
        <h3>{score}</h3>
        <p>{context}</p>
        <a href={link}>learn more</a>
      </div>
    )
  }
}



export default withStreamlitConnection(CardSearchComponent)
